import moment from 'moment-hijri';

export function convertToHijri(gregorianDate: Date): string {
  return moment(gregorianDate).format('iYYYY/iM/iD');
}

export function convertToGregorian(hijriDate: string): Date {
  return moment(hijriDate, 'iYYYY/iM/iD').toDate();
}

export function formatDate(date: Date): string {
  return moment(date).format('YYYY/MM/DD');
}

export function formatHijriDate(date: Date): string {
  return moment(date).format('iYYYY/iM/iD');
}

export function getCurrentHijriDate(): string {
  return moment().format('iYYYY/iM/iD');
}

export function getCurrentGregorianDate(): string {
  return moment().format('YYYY/MM/DD');
}
