'use client';

import { useState } from 'react';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from '@/components/ui/dialog';
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from '@/components/ui/table';
import { exportToExcel, exportToCSV } from '@/lib/exportImport';
import { formatDate, formatHijriDate } from '@/lib/dateUtils';

interface Score {
  id: string;
  contestantName: string;
  competitionName: string;
  score: number;
  date: string;
}

export default function ScoresPage() {
  const [scores, setScores] = useState<Score[]>([]);
  const [isAddDialogOpen, setIsAddDialogOpen] = useState(false);
  const [newScore, setNewScore] = useState({
    contestantName: '',
    competitionName: '',
    score: '',
    date: '',
  });

  const handleAddScore = (e: React.FormEvent) => {
    e.preventDefault();
    const score: Score = {
      id: Math.random().toString(36).substring(7),
      contestantName: newScore.contestantName,
      competitionName: newScore.competitionName,
      score: parseInt(newScore.score),
      date: new Date(newScore.date).toISOString(),
    };
    setScores([...scores, score]);
    setIsAddDialogOpen(false);
    setNewScore({ contestantName: '', competitionName: '', score: '', date: '' });
  };

  const handleExport = (format: 'excel' | 'csv') => {
    const data = scores.map(score => ({
      'اسم المتسابقة': score.contestantName,
      'اسم المسابقة': score.competitionName,
      'الدرجة': score.score,
      'تاريخ التسجيل': formatDate(new Date(score.date)),
      'التاريخ الهجري': formatHijriDate(new Date(score.date)),
    }));

    if (format === 'excel') {
      exportToExcel(data, 'scores');
    } else {
      exportToCSV(data, 'scores');
    }
  };

  return (
    <div className="max-w-7xl mx-auto py-6 sm:px-6 lg:px-8">
      <div className="px-4 py-6 sm:px-0">
        <div className="bg-white overflow-hidden shadow-sm sm:rounded-lg">
          <div className="p-6">
            <div className="flex justify-between items-center mb-6">
              <h2 className="text-2xl font-semibold text-gray-900">الدرجات</h2>
              <div className="flex gap-2">
                <Button onClick={() => handleExport('excel')} variant="outline">
                  تصدير Excel
                </Button>
                <Button onClick={() => handleExport('csv')} variant="outline">
                  تصدير CSV
                </Button>
                <Dialog open={isAddDialogOpen} onOpenChange={setIsAddDialogOpen}>
                  <DialogTrigger asChild>
                    <Button>إضافة درجة</Button>
                  </DialogTrigger>
                  <DialogContent>
                    <DialogHeader>
                      <DialogTitle>إضافة درجة جديدة</DialogTitle>
                    </DialogHeader>
                    <form onSubmit={handleAddScore} className="space-y-4">
                      <div>
                        <label className="block text-sm font-medium text-gray-700 mb-1">
                          اسم المتسابقة
                        </label>
                        <Input
                          type="text"
                          value={newScore.contestantName}
                          onChange={(e) =>
                            setNewScore({ ...newScore, contestantName: e.target.value })
                          }
                          required
                        />
                      </div>
                      <div>
                        <label className="block text-sm font-medium text-gray-700 mb-1">
                          اسم المسابقة
                        </label>
                        <Input
                          type="text"
                          value={newScore.competitionName}
                          onChange={(e) =>
                            setNewScore({ ...newScore, competitionName: e.target.value })
                          }
                          required
                        />
                      </div>
                      <div>
                        <label className="block text-sm font-medium text-gray-700 mb-1">
                          الدرجة
                        </label>
                        <Input
                          type="number"
                          value={newScore.score}
                          onChange={(e) =>
                            setNewScore({ ...newScore, score: e.target.value })
                          }
                          required
                        />
                      </div>
                      <div>
                        <label className="block text-sm font-medium text-gray-700 mb-1">
                          تاريخ التسجيل
                        </label>
                        <Input
                          type="date"
                          value={newScore.date}
                          onChange={(e) =>
                            setNewScore({ ...newScore, date: e.target.value })
                          }
                          required
                        />
                      </div>
                      <div className="flex justify-end gap-2">
                        <Button type="button" variant="outline" onClick={() => setIsAddDialogOpen(false)}>
                          إلغاء
                        </Button>
                        <Button type="submit">حفظ</Button>
                      </div>
                    </form>
                  </DialogContent>
                </Dialog>
              </div>
            </div>
            <Table>
              <TableHeader>
                <TableRow>
                  <TableHead>اسم المتسابقة</TableHead>
                  <TableHead>اسم المسابقة</TableHead>
                  <TableHead>الدرجة</TableHead>
                  <TableHead>تاريخ التسجيل</TableHead>
                  <TableHead>التاريخ الهجري</TableHead>
                  <TableHead>الإجراءات</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {scores.map((score) => (
                  <TableRow key={score.id}>
                    <TableCell>{score.contestantName}</TableCell>
                    <TableCell>{score.competitionName}</TableCell>
                    <TableCell>{score.score}</TableCell>
                    <TableCell>{formatDate(new Date(score.date))}</TableCell>
                    <TableCell>{formatHijriDate(new Date(score.date))}</TableCell>
                    <TableCell>
                      <Button
                        variant="outline"
                        size="sm"
                        onClick={() => {
                          // TODO: Implement edit functionality
                        }}
                      >
                        تعديل
                      </Button>
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </div>
        </div>
      </div>
    </div>
  );
}
